body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  justify-content: center;
  font-family: 'Roboto', sans-serif;
}

.Sidebar {
    display: flex;
    flex-direction: column;
    width: 360px;
    background-color: #333333;
    padding-left: 40px;
}

.Sidebar__sidebar-title {
    margin-top: 50px;
    font-size: 36px;
    line-height: 40px;
    color: #F0F0F0;
}

.Sidebar__sidebar-description {
    margin-top: 20px;
    font-size: 14px;
    width: 320px;
    line-height: normal;
    color: #9B9B9B;
}

.sidebar__delimetr {
    margin-top: 30px;
    margin-left: 0px;
    width: 200px;
    border: 1px solid #5E5E5E;
}

.Sidebar-link {
    margin-top: 30px;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    color: #969696;
    text-decoration: none;
}

.about {
    margin-top: 15px;
}

.Sidebar-icon {
    display: flex;
    margin-top: 30px;
    width: 20px;
    height: 20px;
}

.github-icon {
    margin-left: 20px;
}

.instagram-icon {
    margin-left: 20px;
}

.twitter-icon {
    margin-left: 20px;
}
.Article {
    display: flex;
    flex-direction: column;
    width: 950px;
    height: 100%;
    padding-left: 50px;
    text-align: justify;
}

.article-title {
    /* font-family: Roboto; */
    font-size: 36px;
    padding-top: 44px;
    color: #333333;
}

.article-date {
    font-size: 14px;
    color: #969696;
    padding-top: 15px;
    text-transform: uppercase;
}

.article-text {
    font-size: 14px;
    color: #000000;
    padding-top: 15px;
}

.article__continue-button {
    margin-top: 15px;
    width: 230px;
    height: 35px;
    border-radius: 20px;
    border: 1px solid #D3D3D3;
    background-color: #FFFFFF;
    outline: none;
}

.article__continue-link {
    text-decoration: none;
    color: #000000;
}

.article__delimetr {
    width: 950px;
    border: 1px solid #D3D3D3;
    margin-top: 42px;
    margin-right: 100px;
    margin-bottom: -20px;
}

.article__more-button {
    margin-top: 40px;
    margin-bottom: 40px;
    margin-left: 40%;
    margin-right: 60%;
    width: 200px;
    height: 50px;
    border: 1px solid #D3D3D3;
    border-radius: 3px;
    background-color: #FFFFFF;
    outline: none;
}
.About {
    width: 950px;
    height: 100vh;
    font-size: 36px;
    padding-top: 50px;
    padding-left: 50px;
    color: #333333;
}
.post-layout {
    height: 100vh;
}

.article-author {
    font-size: 12px;
    color: #969696;
    padding-top: 15px;
}

.comments-delimetr {
    width: 950px;
    border: 1px solid #D3D3D3;
    margin-right: 100px;
}

.comments-text {
    text-align: justify;
    font-size: 14px;
    color: #000000;
    width: 950px;
}

.comments-email {
    font-size: 11px;
    color: #969696;
    margin-bottom: -15px;
    margin-top: -15px;
}
